
.shop-view {
  position: relative;
}

/* ‰ΩôÈ¢ùÊòæÁ§∫Âä®Áîª */
.balance-display {
  animation: balance-glow 2s ease-in-out infinite alternate;
}

@keyframes balance-glow {
  from {
    box-shadow: 0 0 5px rgba(236, 72, 153, 0.3);
  }
  to {
    box-shadow: 0 0 15px rgba(236, 72, 153, 0.5);
  }
}

/* ÂàÜÁ±ªÊåâÈíÆ */
.category-btn {
  flex-shrink: 0;
}

/* ÂïÜÂìÅÂç°ÁâáÊÇ¨ÂÅú */
.shop-item {
  &:hover .item-icon {
    animation: item-bounce 0.5s ease;
  }
}

@keyframes item-bounce {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.15);
  }
}

/* ËØ¶ÊÉÖÈù¢ÊùøÊªëÂÖ•Âä®Áîª */
.slide-up-enter-active,
.slide-up-leave-active {
  transition: all 0.3s ease-out;
}

.slide-up-enter-from,
.slide-up-leave-to {
  transform: translateY(100%);
  opacity: 0;
}

/* Ë¥≠‰π∞ÊåâÈíÆÊÇ¨ÂÅúÊïàÊûú */
.purchase-btn:not(:disabled):hover {
  transform: scale(1.02);
}

.purchase-btn:not(:disabled):active {
  transform: scale(0.98);
}

/* ËÉåÊôØÊïàÊûú */
.bg-effects {
  position: absolute;
  inset: 0;
  overflow: hidden;
  z-index: 0;

  .sparkles {
    position: absolute;
    inset: 0;
    background-image:
      radial-gradient(circle at 20% 30%, rgba(236, 72, 153, 0.1) 0%, transparent 50%),
      radial-gradient(circle at 80% 70%, rgba(168, 85, 247, 0.1) 0%, transparent 50%);
  }

  .gradient-orb {
    position: absolute;
    bottom: -100px;
    left: 50%;
    transform: translateX(-50%);
    width: 300px;
    height: 300px;
    border-radius: 50%;
    background: radial-gradient(circle, rgba(236, 72, 153, 0.15) 0%, transparent 70%);
    filter: blur(40px);
  }
}

/* Á°Æ‰øùÂÜÖÂÆπÂú®ËÉåÊôØ‰∏äÂ±Ç */
header,
nav,
main,
.detail-panel {
  position: relative;
  z-index: 1;
}

/* ËØ¶ÊÉÖÈù¢ÊùøÂÆâÂÖ®Âå∫Âüü */
.detail-panel {
  padding-bottom: calc(1rem + env(safe-area-inset-bottom, 0px));
}
