
.xray-container {
  min-height: 120px;
}

.xray-header {
  padding-bottom: 8px;
  border-bottom: 1px solid rgba(0, 255, 136, 0.2);
}

// Vital cards
.vital-card {
  background: rgba(0, 20, 15, 0.6);
  border: 1px solid rgba(0, 255, 136, 0.2);
}

.heart-indicator {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  margin: 0 auto;

  &.heart-normal {
    background: var(--xray-primary);
    animation: heart-pulse 1s ease-in-out infinite;
  }

  &.heart-fast {
    background: #ff6b6b;
    animation: heart-pulse 0.5s ease-in-out infinite;
  }

  &.heart-slow {
    background: #00ccff;
    animation: heart-pulse 1.5s ease-in-out infinite;
  }
}

@keyframes heart-pulse {
  0%,
  100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.3);
    opacity: 0.7;
  }
}

// ECG wave
.ecg-container {
  height: 40px;
  background: rgba(0, 10, 8, 0.8);
  border: 1px solid rgba(0, 255, 136, 0.2);
}

.ecg-wave {
  width: 100%;
  height: 100%;
}

.ecg-path {
  stroke: var(--xray-primary);
  filter: drop-shadow(0 0 3px var(--xray-primary));
}

// Hormone bars
.hormone-bar {
  transition: width 0.4s ease-out;

  &.hormone-dopamine {
    background: linear-gradient(90deg, #ff0066 0%, rgba(255, 0, 102, 0.3) 100%);
    box-shadow: 0 0 8px #ff0066;
  }

  &.hormone-endorphin {
    background: linear-gradient(90deg, #00ff88 0%, rgba(0, 255, 136, 0.3) 100%);
    box-shadow: 0 0 8px #00ff88;
  }

  &.hormone-adrenaline {
    background: linear-gradient(90deg, #ffaa00 0%, rgba(255, 170, 0, 0.3) 100%);
    box-shadow: 0 0 8px #ffaa00;
  }

  &.hormone-oxytocin {
    background: linear-gradient(90deg, #ff66cc 0%, rgba(255, 102, 204, 0.3) 100%);
    box-shadow: 0 0 8px #ff66cc;
  }
}

// Neural & Muscle bars
.neural-card,
.muscle-card {
  background: rgba(0, 20, 15, 0.4);
  border: 1px solid rgba(0, 255, 136, 0.15);
}

.neural-bar {
  background: linear-gradient(90deg, #00ccff 0%, rgba(0, 204, 255, 0.3) 100%);
  box-shadow: 0 0 6px #00ccff;
}

.muscle-bar {
  background: linear-gradient(90deg, #ffaa00 0%, rgba(255, 170, 0, 0.3) 100%);
  box-shadow: 0 0 6px #ffaa00;
}

// Pupil indicator
.pupil-section {
  background: rgba(0, 20, 15, 0.4);
  border: 1px solid rgba(255, 0, 102, 0.2);
}

.pupil-icon {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: radial-gradient(circle, #4a6fa5 0%, #2d4a6a 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid #1a1a2e;
}

.pupil-inner {
  width: calc(var(--pupil-size) * 20px);
  height: calc(var(--pupil-size) * 20px);
  border-radius: 50%;
  background: #000;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.8);
}

// Robot system cards
.sys-card {
  background: rgba(0, 20, 15, 0.6);
  border: 1px solid rgba(0, 204, 255, 0.2);
}

.cpu-indicator {
  width: 10px;
  height: 10px;
  border-radius: 2px;

  &.cpu-normal {
    background: var(--xray-primary);
    box-shadow: 0 0 6px var(--xray-primary);
  }

  &.cpu-warm {
    background: #ffaa00;
    box-shadow: 0 0 6px #ffaa00;
    animation: cpu-blink 1s ease-in-out infinite;
  }

  &.cpu-hot {
    background: #ff4444;
    box-shadow: 0 0 8px #ff4444;
    animation: cpu-blink 0.3s ease-in-out infinite;
  }
}

@keyframes cpu-blink {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

// Robot emotion bars
.memory-bar {
  background: linear-gradient(90deg, #00ccff 0%, rgba(0, 204, 255, 0.3) 100%);
  box-shadow: 0 0 6px #00ccff;
}

.emotion-bar {
  background: linear-gradient(90deg, #ff0066 0%, rgba(255, 0, 102, 0.3) 100%);
  box-shadow: 0 0 6px #ff0066;
}

.awareness-bar {
  background: linear-gradient(90deg, #aa66ff 0%, rgba(170, 102, 255, 0.3) 100%);
  box-shadow: 0 0 6px #aa66ff;
}

.overflow-bar {
  background: linear-gradient(90deg, #ff4444 0%, rgba(255, 68, 68, 0.3) 100%);
  box-shadow: 0 0 6px #ff4444;
}

// Latency indicator
.latency-section {
  background: rgba(0, 20, 15, 0.4);
  border: 1px solid rgba(0, 255, 136, 0.15);
}

.latency-pulse {
  width: 8px;
  height: 8px;
  border-radius: 50%;

  &.latency-low {
    background: var(--xray-primary);
    box-shadow: 0 0 6px var(--xray-primary);
  }

  &.latency-medium {
    background: #ffaa00;
    box-shadow: 0 0 6px #ffaa00;
  }

  &.latency-high {
    background: #ff4444;
    box-shadow: 0 0 6px #ff4444;
    animation: latency-pulse 0.5s ease-in-out infinite;
  }
}

@keyframes latency-pulse {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.3);
  }
}

// Transitions
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}

.target-name {
  white-space: nowrap;
}

// Remote Control Section
.remote-control-section {
  background: rgba(0, 10, 8, 0.4);
  border-radius: 6px;
  padding: 10px;
  margin-top: 12px;
}

.remote-status-dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #444;
  transition: all 0.3s ease;

  &.active {
    background: var(--xray-primary);
    box-shadow: 0 0 8px var(--xray-primary);
    animation: pulse-glow 2s ease-in-out infinite;
  }

  &.locked {
    background: var(--xray-accent);
    box-shadow: 0 0 10px var(--xray-accent);
    animation: pulse-glow-pink 1s ease-in-out infinite;
  }
}

@keyframes pulse-glow {
  0%,
  100% {
    box-shadow: 0 0 8px var(--xray-primary);
  }
  50% {
    box-shadow: 0 0 15px var(--xray-primary);
  }
}

@keyframes pulse-glow-pink {
  0%,
  100% {
    box-shadow: 0 0 8px var(--xray-accent);
  }
  50% {
    box-shadow: 0 0 15px var(--xray-accent);
  }
}

.remote-toggle-btn {
  background: rgba(0, 40, 30, 0.6);
  border: 1px solid rgba(100, 100, 100, 0.3);
  color: #666;
  cursor: pointer;

  &:hover {
    border-color: rgba(0, 255, 136, 0.3);
    color: #888;
  }

  &.active {
    background: rgba(0, 255, 136, 0.1);
    border-color: var(--xray-primary);
    color: var(--xray-primary);
    box-shadow: 0 0 10px rgba(0, 255, 136, 0.2);

    &:hover {
      background: rgba(0, 255, 136, 0.15);
    }
  }
}

.target-lock-section {
  margin-top: 8px;
}

.locked-bg {
  background: rgba(255, 0, 102, 0.1);
  border: 1px solid rgba(255, 0, 102, 0.3);
}

.unlocked-bg {
  background: rgba(0, 40, 30, 0.4);
  border: 1px solid rgba(0, 255, 136, 0.2);
}

.lock-icon {
  color: var(--xray-primary);
  transition: all 0.3s ease;

  &.locked {
    color: var(--xray-accent);
    animation: lock-pulse 1.5s ease-in-out infinite;
  }
}

@keyframes lock-pulse {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}

.lock-btn-active {
  background: rgba(0, 255, 136, 0.2);
  border: 1px solid var(--xray-primary);
  color: var(--xray-primary);
  cursor: pointer;

  &:hover {
    background: rgba(0, 255, 136, 0.3);
    box-shadow: 0 0 8px rgba(0, 255, 136, 0.3);
  }
}

.unlock-btn {
  background: rgba(255, 0, 102, 0.2);
  border: 1px solid var(--xray-accent);
  color: var(--xray-accent);
  cursor: pointer;

  &:hover {
    background: rgba(255, 0, 102, 0.3);
    box-shadow: 0 0 8px rgba(255, 0, 102, 0.3);
  }
}

.signal-section {
  background: rgba(0, 20, 15, 0.5);
  border: 1px solid rgba(0, 255, 136, 0.15);
}

.signal-bars {
  height: 12px;
}

.signal-bar {
  flex: 1;
  height: 100%;
  background: rgba(50, 50, 50, 0.5);
  border-radius: 2px;
  transition: all 0.3s ease;

  &.active {
    background: var(--xray-primary);
    box-shadow: 0 0 4px var(--xray-primary);
  }

  &:nth-child(1) {
    height: 40%;
    align-self: flex-end;
  }
  &:nth-child(2) {
    height: 55%;
    align-self: flex-end;
  }
  &:nth-child(3) {
    height: 70%;
    align-self: flex-end;
  }
  &:nth-child(4) {
    height: 85%;
    align-self: flex-end;
  }
  &:nth-child(5) {
    height: 100%;
  }
}
