
.inventory-view {
  position: relative;
}

/* Á©∫Áä∂ÊÄÅÂä®Áîª */
.empty-state {
  .icon-container {
    position: relative;

    .pulse-ring {
      position: absolute;
      inset: -20px;
      border: 2px solid var(--theme-secondary);
      border-radius: 50%;
      opacity: 0.3;
      animation: pulse-expand 2s ease-out infinite;
    }
  }
}

@keyframes pulse-expand {
  0% {
    transform: scale(0.8);
    opacity: 0.5;
  }
  100% {
    transform: scale(1.5);
    opacity: 0;
  }
}

/* Áâ©ÂìÅÂç°ÁâáÈÄâ‰∏≠ÊåáÁ§∫Âô® */
.selected-indicator {
  &::before {
    content: '';
    position: absolute;
    inset: -2px;
    border: 2px solid var(--theme-color);
    border-radius: inherit;
    animation: selected-pulse 1.5s ease-out infinite;
  }
}

@keyframes selected-pulse {
  0% {
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(1.1);
  }
}

/* ËØ¶ÊÉÖÈù¢ÊùøÊªëÂÖ•Âä®Áîª */
.slide-up-enter-active,
.slide-up-leave-active {
  transition: all 0.3s ease-out;
}

.slide-up-enter-from,
.slide-up-leave-to {
  transform: translateY(100%);
  opacity: 0;
}

/* ËÉåÊôØÁΩëÊ†ºË£ÖÈ•∞ */
.bg-decor {
  position: absolute;
  inset: 0;
  overflow: hidden;
  z-index: 0;

  .grid-overlay {
    position: absolute;
    inset: 0;
    background-image:
      linear-gradient(rgba(255, 0, 255, 0.03) 1px, transparent 1px),
      linear-gradient(90deg, rgba(255, 0, 255, 0.03) 1px, transparent 1px);
    background-size: 30px 30px;
  }
}

/* Á°Æ‰øùÂÜÖÂÆπÂú®Ë£ÖÈ•∞‰∏äÂ±Ç */
header,
main,
.detail-panel {
  position: relative;
  z-index: 1;
}

/* Áâ©ÂìÅÁΩëÊ†ºÂìçÂ∫îÂºè */
@media (min-width: 400px) {
  .item-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

/* ËØ¶ÊÉÖÈù¢ÊùøÂÆâÂÖ®Âå∫Âüü */
.detail-panel {
  padding-bottom: calc(1rem + env(safe-area-inset-bottom, 0px));
}

/* Ê∑°ÂÖ•Ê∑°Âá∫Âä®Áîª */
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
