
.hypnosis-view {
  position: relative;
}

/* ÁõÆÊ†áÂç°ÁâáËÑâÂÜ≤ */
.target-card {
  position: relative;

  .pulse-ring {
    position: absolute;
    inset: -4px;
    border: 2px solid rgba(168, 85, 247, 0.5);
    border-radius: inherit;
    animation: pulse-expand 1.5s ease-out infinite;
    pointer-events: none;
  }
}

@keyframes pulse-expand {
  0% {
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(1.15);
  }
}

/* Ëû∫ÊóãÂõæÊ†áÂä®Áîª */
.spiral-icon {
  animation: slow-spin 10s linear infinite;
}

@keyframes slow-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* ËÉåÊôØËû∫ÊóãÊïàÊûú */
.bg-effects {
  position: absolute;
  inset: 0;
  overflow: hidden;
  z-index: 0;

  .spiral-bg {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 200%;
    height: 200%;
    transform: translate(-50%, -50%);
    background: conic-gradient(from 0deg, transparent 0%, rgba(168, 85, 247, 0.03) 25%, transparent 50%);
    animation: slow-spin 30s linear infinite;
  }
}

/* Á°Æ‰øùÂÜÖÂÆπÂú®ËÉåÊôØ‰∏äÂ±Ç */
header,
main {
  position: relative;
  z-index: 1;
}

/* Áä∂ÊÄÅË°åÊÇ¨ÂÅúÊïàÊûú */
.stat-row {
  &:hover {
    .h-2 {
      height: 10px;
    }
  }
}

/* ÊöóÁ§∫È°πÊÇ¨ÂÅú */
.suggestion-item {
  &:hover {
    transform: translateX(4px);
  }
}
